//
// Typography
// --------------------------------------------------
@import url('https://fonts.googleapis.com/css?family=Love+Ya+Like+A+Sister');
@import url('https://fonts.googleapis.com/css?family=Short+Stack');
@import "mixins/fontface";


// Fonts

@include font-face-mixin('Nexa', '../fonts/nexa', 'nexa_light-webfont');
@include font-face-mixin('Nexa Bold', '../fonts/nexa', 'nexa_bold-webfont');



// Font Mixins

@mixin Nexa {
  font-family: 'Nexa';
}

@mixin NexaBold {
  font-family: 'Nexa Bold';
}



