body{
	background-color: #6F5547;
	height: 100vh;
	background-image: url("../img/main.svg");
	background-position: center;
	background-size: contain;
	background-repeat: no-repeat;
	&.mypage{
		background-image: url("../img/main-my.svg");
		main{
			overflow: hidden;
		}
	}
}

main{
	height: 100vh;
	// overflow: hidden;
	padding: 1.5em;
	max-width: 1400px;
	margin: 0 auto;
	position: relative;
	display: flex;
	align-items: center;
	flex-wrap: nowrap;
	// justify-content: center;
	justify-content: baseline;
	flex-direction: column;
	// width:80%;
	h2{
		text-align: center;
		font-family: 'Short Stack', cursive;
		font-size: 2rem;
		background-color: #fff;
		border-radius: 20px;
		box-sizing: border-box;
		padding: 1em 1.5em;
		position: relative;
		&:before{
			width: 0px;
			height: 0px;
			border-style: solid;
			border-width: 10px 15px 10px 0;
			border-color: transparent #dd4397 transparent transparent;
		}
	}

}

.main-wrapper{
	display:flex;
	flex-direction: column;
	max-width: 600px;
}

.login-box{
	width: 435px;
	min-height: 445px;
	overflow: hidden;
	margin: 0 auto;
	// background-color: #fff;
	padding: 3em 3.5em;
	// background-color: #fbb03b;
	// background-image: url("../img/login-bg-new.png");
	// background-size: 100% 105%;
	// background-repeat: no-repeat;
	background-color: rgba(241,184,155,0.9);
	border-radius: 50px;
	border: 10px solid #FCE2D3;
	position: relative;
	flex-basis: 100%;
	&:before{
		content: " ";
		position: absolute;
		z-index: 0;
		top: -3px;
		left: 0px;
		right: 0px;
		bottom: -3px;
		border: 10px solid #FCB99C;
		border-radius: 50px;
	}
}

#menu-item img:hover ~ #menu-item img{
	opacity: 0.8;
	transition: mdc-animation-exit-permanent(all,175ms,150ms);
}

.greeting-box, #option-box{
	// background-image: url("../img/textbb-1.png");
	// background-position: center;
	// background-size: 100% 100%;
	// box-sizing: border-box;
	@extend .login-box;
	min-height: 0;
	flex-basis: 0;
	width: inherit;
	height: 100%;
	padding: 1em 2em 3em;
	h2, h3{
		// color: #fff;
	}
	h5{
		color: #cf0200;
	}
}

#entry-box{
	@extend .greeting-box;
	// background-image: url("../img/textbbox-1.png");
	box-sizing: border-box;
	padding: 1em 5em 3em 2em;
}

.view-box-wrapper{
	z-index: 99;
}
textarea#entry_text{
	background-color: white;
	  border: 5px solid #FDD649;
	  // border-bottom: $input-border;
	  border-radius: 5px;
	  resize: vertical; 
	  min-height: 180px;
	  @extend .z-depth-1;
}

#menu-item{
	margin-top: 1em;
}

img#entry_pic{
	max-width: 150px;
}



#submit-login, #submit-register{
	cursor: pointer;
}

#submit-login{
	float: right;
	font-family: 'Love Ya Like A Sister', cursive;
	background-color: #EE8533;
	padding: 10px 20px;
	font-size: 2rem;
	border-radius: 20px;
	border: 5px solid #F1D3C0;
}

#submit-register{
	@extend #submit-login;
}


#container-login{
	position: relative;
	top: 30px;
	z-index: 99;
	transition: mdc-animation-exit-permanent(all,175ms,150ms);
	h2{
		font-size: 2.5rem;
		color: #cf0200;
	}
	label{
		font-family: 'Love Ya Like A Sister', cursive;
		font-size: 2rem;
		margin-left: 10px;
	}
	&.anim-up{
		top: -350px;
		transition: mdc-animation-enter(all,175ms,150ms);
	}
}

label{
	color: #603913;
}

.sign-wrap{
	display: block;
	text-align: center;
}

.sign-up, .sign-in{
	// display: inline-block;
	// float: right;
	margin-top: 1em;
	margin-right: 1.5em;
	transition: mdc-animation-exit-permanent(all,175ms,150ms);
	&:hover{
		transition: mdc-animation-enter(all,175ms,150ms);
		cursor: pointer;
		color: blue;
		text-decoration: underline;
	}
}

.container-signup{
	position: relative;
	margin-top: 4em;
	top: 150px;
	// background-color: #fbb03b;
	transition: mdc-animation-exit-permanent(all,175ms,150ms);
	z-index: 999;
	&.anim-up{
		top: -300px;
		transition: mdc-animation-enter(all,175ms,150ms);
	}
	h1{
		font-size: 2rem;
		margin-bottom: 0;
	}
	label{
		font-family: 'Love Ya Like A Sister', cursive;
		font-size: 2rem;
		margin-left: 10px;
	}
}

.logo{
	text-align: center;
	flex-basis:50%;
	img{
		// width: 300px;
		max-height: 180px;
	}
}


.logopage{
	position: absolute;
	left: 10px;
	bottom: 50px;
	img{
		width: 300px;
	}
}

.displaylog-container{
	height:100vh;
	position: fixed;
	top: 0;
	right: -33%;
	width: 33%;
	background-color: #5A3C1F;
	transition: box-shadow .25s;
	border-radius: 2px;
	z-index: 2;
	@extend .z-depth-5;
	// opacity: 0.5;
	transition: mdc-animation-exit-permanent(all,175ms,150ms);
	&.slide-in{
		transition: mdc-animation-enter(all,175ms,150ms);
		right: 0;
	}
}

div#displaylogdis{
	margin-top: 12em;
	margin-bottom: 5em;
	// padding-left: 2em;
	// padding-right: 2em;
	float:none;
	height: 60%;
	overflow-y: scroll;
	transition: box-shadow .25s;
	border-radius: 2px;
	background-color: #ffffff;
	@extend .z-depth-5;
	div:nth-of-type(2n+1){
		background-color: #fbdcac;
	}
	div{
		padding: 0 2em 1em;
		box-sizing:border-box;
		overflow: auto;
	}
}


.logout-wrapper{
	position: absolute;
	right: 50px;
	bottom: 100px;
	img{
		width: 90px;
		&:hover{
			cursor: pointer;
		}
	}
}

.view-box-container{
	width: 500px;
	margin: 0 auto;
	justify-content: center;
}

.main-button{
	position:absolute;
	right: 80px;
	top: 100px;
	z-index: 99;
	img{
		padding-left: 1em;
		width: 80px;
		&:hover{
			cursor: pointer;
		}
	}
}

#main-display, #option-box, #menu-item{
	// padding-top: 1em;
	position: relative;
}

#option-box, #text-success, #menu-item{
	opacity: 0;
}

#text-success{
	h2{
		color: #cf0200;
	}
}
#entrylogdis_text{
	background-color: rgba(255,255,255, 0.6);
	padding: 10px;
	h1{
		margin-top: 0;
	}
}

#entrylogdis_pic{
	max-width: 200px;
}

.entry-title{
	transition: mdc-animation-exit-permanent(all,175ms,150ms);
	position: relative;
	top: 0;
	opacity: 1;
	height: auto;
	&.hidden{
		transition: mdc-animation-enter(all,175ms,150ms);
		position: relative;
		top:50px;
		opacity: 0;
		height: 0;
		overflow-y: hidden;
	}
}

#displaylogbig{
	@extend .entry-title;
	@extend .login-box;
	img{
		max-width: 300px;
	}
	button{
		position: relative;
	}
}

.view-title{
	@extend .entry-title;
}

.text-box-wrapper{
	position: relative;
	flex-basis: 100%;
	// max-height: 550px;
	// overflow-y: auto;
}

#intro-wrapper{
	transition: mdc-animation-exit-permanent(all,175ms,150ms);
	position: relative;
	top: 0;
	opacity: 1;
	height: auto;
	&.hidden{
		transition: mdc-animation-enter(all,175ms,150ms);
		position: relative;
		top:50px;
		opacity: 0;
		height: 0;
		overflow-y: hidden;

	}
}

#entry-box{
	transition: mdc-animation-exit-permanent(all,175ms,150ms);
	position: relative;
	top: 0;
	opacity: 1;
	height: auto;
	overflow-y: visible;
	&.hidden{
		transition: mdc-animation-enter(all,175ms,150ms);
		position: relative;
		top:50px;
		opacity: 0;
		height: 0;
		overflow-y: hidden;

	}
}

.dog-placement{
	position: absolute;
	right: 15%;
	bottom: 15%;
	img{
		max-width: 200px;
	}
}

#qtext{
	background-color: white;
	border: 5px solid #FDD649;
	// border-bottom: $input-border;
	border-radius: 5px;
	resize: vertical; 
	min-height: 180px;
	@extend .z-depth-1;
}


.ql-toolbar.ql-snow {
	background-color: beige;
}


#chat{
	// position: absolute;
	// top:0;
	&.hidden{
		transition: mdc-animation-enter(all,175ms,150ms);
		position: relative;
		top:50px;
		opacity: 0;
		height: 0;
		overflow-y: hidden;

	}
}

.botui-container{
	border-radius: 20px;
}